import React from "react";
import styles from "./Education.module.css";
import common from "../../../Style/CommonStyles.module.css";
import Pagination from "../../../Components/DetailComponents/Pagination";

const Education = () => {
  return (
    <div className={styles.container}>
      <div className={`${common.btnWrap} ${styles.btnWrap}`}>
        <button className={common.btnFocus}>창업교과</button>
        <button>창업 비교과</button>
        <button>창업동아리</button>
        <button>창업자 현황</button>
      </div>
      <p className={styles.title}>창업정규교과목 운영 현황</p>
      <div className={`${styles.searchContainer} ${common.searchContainer}`}>
        <div className={common.searchWrap}>
          <select>
            <option value="default">내용</option>
          </select>
          <input placeholder="검색어를 입력하세요." />
          <img className={common.searchIcon} src="Icon/magnifyingGlass.png" alt="search" />
        </div>
        <select className={common.sort}>
          <option value="default">최신순</option>
        </select>
      </div>
      <div className={styles.tableWrap}>
        <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
          <li>NO</li>
          <li>교과목 명칭</li>
          <li>학기</li>
          <li>학점</li>
          <li>참여인원</li>
          <li>전공구분</li>
          <li>비고</li>
        </ul>
        <ul className={`${common.graphList} ${styles.graphWidth}`}>
          <li>26</li>
          <li>기업가정신</li>
          <li>2022-1학기</li>
          <li>1</li>
          <li>1277</li>
          <li>교양</li>
          <li>사이버 강좌</li>
        </ul>
        <ul className={`${common.graphList} ${styles.graphWidth}`}>
          <li>40</li>
          <li>기술과발명특허</li>
          <li>2022-2학기</li>
          <li>2</li>
          <li>35</li>
          <li>일반</li>
        </ul>
      </div>
      <Pagination />
    </div>
  );
};

export default Education;
