import React from "react";
import styles from "./Founded.module.css";
import common from "../../../Style/CommonStyles.module.css";
const Founded = () => {
  return (
    <div className={styles.Container}>
      <div className={`${common.btnWrap} ${styles.btnWrap}`}>
        <button>AD</button>
        <button className={common.btnFocus}>일머리 PBL</button>
        <button>CD 교과/비교과</button>
        <button>현장실습</button>
      </div>
      <div className={styles.infoWrap}>
        <p className={styles.title}>PBL (Project-Based Learning)</p>
        <p className={styles.info}>
          실질적 문제 해결을 통해 <span>학습자의 능동적 학습을 촉진시키기 위한 학습자 중심</span>의 교수·학습 방법 <br />
          학생들에게 실질적인 문제를 제시하여 학생들 상호 간에 공동으로 문제 해결 방안을 강구하고,
          <br />
          <span>개별 학습과 협동 학습을 통해 스스로 학습</span>이 이루어지게 한다
        </p>

        <div className={styles.iconWrap}>
          <div className={styles.first}>
            <div className="iconInfoWrap">
              <img src="Icon/pbl06.png" alt="icon1" />
              <p>
                실제적 질문과 <br />
                문제상황
              </p>
            </div>
            <div className="iconInfoWrap">
              <img src="Icon/pbl01.png" alt="icon2" />
              <p>핵심개념</p>
            </div>
          </div>
          <div className={styles.second}>
            <div className="iconInfoWrap">
              <img src="Icon/pbl05.png" alt="icon2" />
              <p>결과물 공유</p>
            </div>
            <p className={styles.circle}>
              PBL <br />
              핵심요소
            </p>
            <div className="iconInfoWrap">
              <img src="Icon/pbl02.png" alt="icon2" />
              <p>
                학생 주도
                <br />
                배움
              </p>
            </div>
          </div>
          <div className={styles.third}>
            <div className="iconInfoWrap">
              <img src="Icon/pbl04.png" alt="icon2" />
              <p>성찰</p>
            </div>
            <div className="iconInfoWrap">
              <img src="Icon/pbl03.png" alt="icon2" />
              <p>
                피드백과
                <br /> 재도전
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.workshopWrap}>
        <p className={styles.title}>일머리 PBL 교수법 워크숍</p>
        {/**info text */}
        <p className={styles.info}>
          4차 산업혁명시대에 일머리있는 미래인재를 양성하기 위한 일머리 PBL 교수법 워크숍 진행
          <br />
          LINC3.0사업단 일머리PBL센터(센터장 정은희)에서 LINC3.0사업 참여학과(부)와 일머리PBL 담당 전임교원을 대상
        </p>
        <img src="Images/pbl.jpg" alt="pbl pic" />
      </div>
    </div>
  );
};

export default Founded;
