import React from "react";
import styles from "./Join.module.css";

const Join = () => {
  return (
    <div className={styles.container}>
      <div className={styles.joinForm}>
        <img src="Icon/linclogo.png" alt="로고" />
        <h2>회원가입</h2>
        <div className={styles.inputWrap}>
          <div className={styles.inputForm}>
            <p>아이디</p>
            <input type="text" placeholder="이메일 주소" className={styles.loginBox} />
            <em>중복확인</em>
            <p>비밀번호</p>
            <input type="password" placeholder="비밀번호 8자리 이상" className={styles.loginBox} />
            <p>비밀번호 확인</p>
            <input type="password" placeholder="비밀번호 확인" className={styles.loginBox} />
            <p>이름</p>
            <input type="text" placeholder="한글 두글자 이상" className={styles.loginBox} />
            <p>휴대폰 번호</p>
            <input type="text" placeholder="'-'구분없이 입력" className={styles.loginBox} />
            <em>인증번호 전송</em>
            <p>인증번호</p>
            <input type="text" placeholder="인증번호 입력" className={styles.loginBox} />
            <p>생년월일</p>
            <input type="text" placeholder="숫자 8자리" className={styles.loginBox} />
          </div>

          <div className={styles.agreement}>
            <div>
              <input type="radio" />
              <p>모두 동의합니다.</p>
            </div>
            <div>
              <input type="radio" />
              <p>이용약관 동의</p>
            </div>
            <div>
              <input type="radio" />
              <p>개인정보 취급방침 동의</p>
            </div>
            <div>
              <input type="radio" />
              <p>마케팅 정보 수신 동의 (선택)</p>
            </div>
          </div>
          <button className={styles.joinButton}>회원가입</button>
        </div>
      </div>
    </div>
  );
};

export default Join;
