import React from "react";
import styles from "./Prototype.module.css";
const Prototype = () => {
  return (
    <div className={styles.container}>
      <div className={styles.infoWrap}>
        <h2 className={styles.infoTitle}>개요</h2>
        <li>시제품 제작과 관련하여 애로를 겪고 있는 가족회사와 연계하여 공동으로 시제품 제작을 지원하는 프로그램</li>
        <h2 className={styles.infoTitle}>목적</h2>
        <li>가족회사의 기술경쟁력 강화와 신규 사업아이템 창출</li>
        <li>시제품 제작지원을 통해 시제품 사업화를 지원하고 가족회사 경쟁력 강화</li>
        <h2 className={styles.infoTitle}>지원내용</h2>
        <li>시제품 설계 및 제작(기계부품/설계/제작)</li>
        <li>업체에 시제품 제작에 필요한 재료비 지원(기업체 대응자금 필요)</li>
        <h2 className={styles.infoTitle}>운영절차</h2>
      </div>
      <div className={styles.processWrap}>
        <div className={styles.process}>
          <div className={styles.imgWrap}>
            <img src="Icon/prototype01.png" alt="prototype1" />
          </div>
          <p className={styles.title}>지원사업공고</p>
          {/**bar*/}
          <p className={styles.info}>
            이메일 및 가족회사 <br /> 포럼을 통한 사업 홍보
          </p>
        </div>

        <div className={styles.process}>
          <div className={styles.imgWrap}>
            <img src="Icon/prototype02.png" alt="prototype2" />
          </div>
          <p className={styles.title}>신청서 제출</p>
          {/**bar*/}
          <p className={styles.info}>
            시제품 지원을 필요로 <br /> 하는 기업의 신청서 검토
          </p>
        </div>

        <div className={styles.process}>
          <div className={styles.imgWrap}>
            <img src="Icon/prototype03.png" alt="prototype3" />
          </div>
          <p className={styles.title}>시제품 설계/제작</p>
          {/**bar*/}
          <p className={styles.info}>시제품 설계 및 제작</p>
        </div>

        <div className={styles.process}>
          <div className={styles.imgWrap}>
            <img src="Icon/prototype04.png" alt="prototype4" />
          </div>
          <p className={styles.title}>결과보고서 제출</p>
          {/**bar*/}
          <p className={styles.info}>
            시제품 완성 후 <br /> 결과 보고서 제출
          </p>
        </div>
      </div>
    </div>
  );
};

export default Prototype;
