import React from "react";
import styles from "./Space.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLocationDot } from "@fortawesome/free-solid-svg-icons";

const Space = () => {
  return (
    <div className={styles.container}>
      <div className={styles.infoWrap}>
        <img src="Images/meetingRoom.jpg" alt="meetingRoom" />
        <div className={styles.roomInfo}>
          <p className={styles.title}>산학협력세미나실</p>
          <p className={styles.info}>
            산학협력세미나실은 산학협력 및 각종 학술행사 시 필요한 경우 신청하여 주시고, 예약 신청 후 <span>세미나실 사용신청서</span>를
            산학협력단에 제출하여 주시기 바랍니다.
          </p>
          <button>세미나실 사용신청서</button>
          <div className={styles.locationWrap}>
            <FontAwesomeIcon icon={faLocationDot} />
            <p className={styles.locationInfo}>회의실 위치</p>
          </div>
          <p className={styles.locationDetail}>(51767) 경상남도 창원시 마산합포구 경남대학로 7(월영동) 경남대학교 산학협력단</p>
          <p className={styles.warning}>
            &#8251;강의 및 수업(학부,대학원)용도로 신청(사용)하실수 없습니다.
            <br />
            산학협력세미나실은 일자 기준으로 1개월 이내까지만 신청이 가능합니다.
          </p>
        </div>
      </div>
      <div className={styles.calender}>
        <img className={styles.timeLine} src="Images/tempTimeLine.png" alt="timeline" />
      </div>
    </div>
  );
};

export default Space;
