import React from "react";
import styles from "./Researcher.module.css";
import InfoNameCard from "../../../Components/InfoNameCard/InfoNameCard";
import data from "../../../Libs/Datas/Main/Researcher.json";
import common from "../../../Style/CommonStyles.module.css";
const Researcher = () => {
  const teamList = ["산학기획팀", "산학연구팀", "산학재무팀", "산학구매팀", "산학감사팀", "부속기관"];
  return (
    <div className={styles.container}>
      <div className={styles.search}>
        <input className={styles.searchBar} placeholder="직원을 검색하세요." />
        <img className={styles.searchIcon} src="Icon/magnifyingGlass.png" alt="search" />
      </div>
      <div className={`${common.btnWrap} ${styles.teamList}`}>
        {teamList.map((name) => (
          <button className={styles.eachTeam}>{name}</button>
        ))}
      </div>
      <div className={styles.subContainer}>
        <div className={styles.teamWrap}>
          {/**중복코드  */}
          <button className={styles.eachTeam}>전체 &#62;</button>
          <button className={styles.eachTeam}>산학기획팀 &#62;</button>
          <button className={styles.eachTeam}>산학연구팀 &#62;</button>
          <button className={`${styles.eachTeam} ${styles.focusTeam}`}>산학재무팀 &#62;</button>
          <button className={styles.eachTeam}>산학구매팀 &#62;</button>
          <button className={styles.eachTeam}>산학감사팀 &#62;</button>
          <button className={styles.eachTeam}>부속기관 &#62;</button>
        </div>
        <div className={styles.personWrap}>
          <p className={styles.title}>산학재무팀</p>
          <div className={styles.cardWrap}>
            {data.map((prop) => (
              <InfoNameCard
                image={prop.image}
                team={prop.team}
                name={prop.name}
                contact={prop.contact}
                position={prop.position}
                email={prop.email}
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Researcher;
