import React from "react";
import styles from "./Patent.module.css";
import DetailedSearch from "../../../Components/DetailComponents/DetailedSearch";
import common from "../../../Style/CommonStyles.module.css";
import Pagination from "../../../Components/DetailComponents/Pagination";

const Patent = () => {
  const dummyData = [
    { id: 1, images: "Icon/patent01.png", titleText: "발명의 신고 (발명자 → 산학협력단)" },
    { id: 2, images: "Icon/patent02.png", titleText: "산학협력단 접수 및 직무발명심의위원회 개최" },
    { id: 3, images: "Icon/patent03.png", titleText: "발명신고서 접수" },
    { id: 4, images: "Icon/patent04.png", titleText: "명세서 작성, 검토 및 출원지시" },
    { id: 5, images: "Icon/patent05.png", titleText: "출원완료 보고 및 확인" },
    { id: 6, images: "Icon/patent06.png", titleText: "출원비용 청구 및 지출" },
    { id: 7, images: "Icon/patent07.png", titleText: "특허청 심사 진행" },
    { id: 8, images: "Icon/patent08.png", titleText: "등록결정 확인 및 특허등록" },
    { id: 9, images: "Icon/patent06.png", titleText: "등록비용 청구 및 지출" },
  ];

  return (
    <div className={styles.container}>
      <p className={styles.subTitle}>
        지식재산이란 인간의 창조적 활동 또는 경험 등에 의하여 창출되거나 발견된 지식˙정보˙기술˙사상이나 물건의 표시, 생물의 품종, 유전자원,
        그 밖에 무형적인 것으로서 재산적 가치가 실현될 수 있는 것을 말함
      </p>
      <div className={common.containerWrap}>
        <div>
          <h2 className={styles.marginEdit}>지식재산권의 역할</h2>
          <li className={styles.liMarker}>
            경쟁자에 대한 시장진입장벽 구축 및 공격으로부터의 보호, 마케팅상의 이점, 로열티를 창출하는 재산으로서의 가치
          </li>
        </div>

        <div>
          <h2>지식재산권의 종류</h2>
          <table className={styles.patentTable}>
            <thead className={styles.patentTh}>
              <tr>
                <th>대분류</th>
                <th>중분류</th>
                <th>소분류</th>
              </tr>
            </thead>
            <tbody className={styles.patentTb}>
              <tr>
                <td rowSpan={4}>전통적지식재산권</td>
                <td rowSpan={2}>산업재산권</td>
                <td> 특허</td>
              </tr>
              <tr>
                <td>실용신안</td>
              </tr>
              <tr>
                <td rowSpan={2}>저작권</td>
                <td>디자인</td>
              </tr>
              <tr>
                <td>상표</td>
              </tr>
              <tr>
                <td rowSpan={4}>신지식재산권</td>
                <td rowSpan={4}>첨단산업재산권</td>
                <td>반도체설계, 생명공학기술</td>
              </tr>
              <tr>
                <td>검퓨터프로그램, 소프트웨어</td>
              </tr>
              <tr>
                <td>데이터베이스, 영업비밀, 뉴미디어</td>
              </tr>
              <tr>
                <td>프랜차이징, 지리적표시, 캐릭터, 인터넷도메인</td>
              </tr>
            </tbody>
          </table>
        </div>

        <div>
          <h2>지식재산 출원 및 등록 절차</h2>
          <div className={styles.procedure}>
            {dummyData.map((data) => (
              <div key={data.id} className={styles.proItem}>
                <span className={styles.proNum}>{data.id}</span>
                <div className={styles.proData}>
                  <img src={data.images} alt={data.titleText} />
                  <p>{data.titleText}</p>
                </div>
                <img src="Icon/patentDot.png" alt="아이콘" className={styles.patentDot} />
              </div>
            ))}
          </div>
        </div>

        <div className={styles.patentList}>
          <h2>특허 현황</h2>
          <DetailedSearch />
          <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
            <li>NO</li>
            <li>발명의 명칭</li>
            <li>특허등록번호</li>
            <li>특허등록일자</li>
            <li>대표발명자</li>
          </ul>
          <ul className={`${common.graphList} ${styles.graphWidth}`}>
            <li>07</li>
            <li>내식성이 우수한 용융 알루미늄도금 탄소강관</li>
            <li>10-2275785</li>
            <li>2023-05-25</li>
            <li>김종렬</li>
          </ul>
        </div>
        <Pagination />
      </div>
    </div>
  );
};

export default Patent;
