import React from "react";
import styles from "./TechnologyTransfer.module.css";
const TechnologyTransfer = () => {
  return (
    <div className={styles.container}>
      {/*meaning of tech transfer */}
      <p className={styles.meaning}>
        기술이전은 연구개발로 취득한 권리 또는 기술을 실시하고자 하는 자에게 이전하거나 실시를 하여하는 것을
        <br /> 의미하며 포괄적인 의미에서의 기술이전은 권리이전, 라이센싱, 노하우 전수 등을 모두 포함한다.
      </p>

      <div className={styles.targetWrap}>
        <h2 className={styles.title}>대상 및 형태</h2>

        <table className={styles.tableWrap} border={1}>
          <tr>
            <th className={`${styles.tableTitle} ${styles.leftEnd}`}>기술이전 형태</th>
            <th className={styles.tableTitle} colSpan={4}>
              내용
            </th>
          </tr>
          <tr>
            <th className={styles.leftEnd}>
              기술매매 <br />
              (양수, 양도)
            </th>
            {/**하위 내용 반복문 돌려서 출력하기  */}
            <td colSpan={4}>매매 형태로 이루어지는 기술이전으로서 기술도입자가 대가를 지불하고 특허권 등의 권리를 명의이전 받는 형식.</td>
          </tr>
          <tr>
            <th rowSpan={2}>라이센스 허여</th>
            <td className={styles.content}>전용 실시권</td>
            <td>
              기술공급자-도임자 계약에 의하여 실시권 범위내에서 해당 특허기술을 독점적으로 사용할 수 있는 권리. <br /> 특허권자는 기술을
              업으로 사용할 수 없음.
            </td>
          </tr>
          <tr>
            <td className={styles.content}>통상 실시권</td>
            <td>
              기술공급자-도임자 계약에 의하여 실시권 범위내에서 해당 특허기술을 비독점적으로 사용할 수 있는 권리. <br /> 특허권자가 제
              3자에게 별도의 통상실시권 허여를 할 수 있음.
            </td>
          </tr>
          <tr>
            <th>
              기술관련 경영지원과
              <br /> 함께하는 이전방식
            </th>
            <td colSpan={3}>기술거래의 대상을 관련된 자본, 경영노하우, 설비 핵심부품 등으로 하여 관련 경영지원을 함께 거래하는 방식.</td>
          </tr>
          <tr>
            <th>라이센스 허여</th>
            <td colSpan={3}>기술자 파견 등을 통해 기술자문 및 지도를 하는 방식.</td>
          </tr>
          <tr>
            <th>라이센스 허여</th>
            <td colSpan={3}>기술자료의 매매에 의한 거래방식, 기술제휴에 의한 협력, 공동연구, 생산제휴 등.</td>
          </tr>
        </table>
      </div>

      <h2 className={styles.title}>기술이전 절차</h2>
      <div className={styles.process1}>
        <div className={`${styles.box} ${styles.box1}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color1}`}>01</p>
          </div>
          <p>기술이전 대상기술 발굴</p>
          <li>기업체 요청 기술</li>
          <li>발명평가결과 우수기술</li>
          <li>개발단계 평가결과 사업화 근접기술</li>
        </div>

        <div className={`${styles.box} ${styles.box2}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color2}`}>02</p>
          </div>
          <p>기술 개발자 명단</p>
          <li>기술이전 가능성 확인</li>
          <li>적정 기술료 산정</li>
        </div>

        <div className={`${styles.box} ${styles.box3}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color3}`}>03</p>
          </div>
          <p>기술 마케팅 전략수립</p>
          <li>기술별 마케팅 전략</li>
          <li>기술금융 및 정부과제 연계 방안</li>
          <li>기술설명자료 준비</li>
        </div>

        <div className={`${styles.box} ${styles.box4}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color4}`}>04</p>
          </div>
          <p>수요기업 발굴</p>
          <li>기술설명회 개최</li>
          <li>기술 중계기관 협력</li>
        </div>
      </div>
      <div className={styles.process2}>
        <div className={`${styles.box} ${styles.box1}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color1}`}>05</p>
          </div>
          <p>수요기업 접촉 및 의견조율</p>
          <li>기술 마케팅</li>
          <li>업체 기술이전 가능성 파악</li>
        </div>

        <div className={`${styles.box} ${styles.box2}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color2}`}>06</p>
          </div>
          <p>기술이전 조건 협의</p>
          <li>협상전략 준비</li>
        </div>

        <div className={`${styles.box} ${styles.box3}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color3}`}>07</p>
          </div>
          <p>협상진행</p>
          <li>기술료 협상</li>
          <li>연구과제 창출 등 논의</li>
        </div>

        <div className={`${styles.box} ${styles.box4}`}>
          <div className={styles.numWrap}>
            <p className={`${styles.number} ${styles.color4}`}>08</p>
          </div>
          <p>계약 및 사후관리</p>
          <li>기술이전 계약 체결</li>
          <li>상용화지원 및 기술료 징수</li>
          <li>애로기술 지원</li>
        </div>
      </div>
      <h2 className={`${styles.title} ${styles.titleMargin}`}>보상금</h2>
      <div className={styles.compensation}>
        <p>
          관련근거: 『강원대학교 지식재산권에 관한 규정』(제5장)
          <br />
          지식재산권 처분 및 실시에 따른 보상금이 지급되며, 각종 자문(기술, 법률, 경영 등)에 따른 보상금 지급
        </p>

        <li>지식재산권 이전의 경우: 발명자 70% : 대학교(산학협력단) 30%</li>
        <li>지식재산권이 없는 경우(노하우의 경우): 발명자 90% : 대학교(산학협력단) 10%</li>
        <li>자문의 경우: 자문교수 90% : 대학교(산학협력단) 10%</li>
      </div>
    </div>
  );
};

export default TechnologyTransfer;
