import React from "react";
import { Link } from "react-router-dom";
import styles from "./UniversityInfo.module.css";
import common from "../../../Style/CommonStyles.module.css";
import Pagination from "../../../Components/DetailComponents/Pagination";

const UniversityInfo = () => {
  return (
    <div className={styles.container}>
      <div className={styles.title}>
        <p>기업과 지역사회의 목소리에 꾸준히 귀기울이며 대학이 보유한 기술과 자원을 활용해 더 나은 내일을 제안합니다.</p>
      </div>

      <div className={styles.infoWrap}>
        <h2 className={styles.infoTitle}>목적</h2>
        <li>기업의 기술개발 지원 및 산학연계 강화</li>
        <li>기업의 애로기술 해소를 통한 기업경쟁력 강화</li>
        <li>클러스터 분야별 융합 연구개발</li>
        <h2 className={styles.infoTitle}>지원사업비</h2>
        <li>사업단(정부) 지원금:사업비 범위 내 기술평가위원회 평가 결과에 따라 차등 지급</li>
        <li>참여기업 부담금:'현금+현물'</li>
        <ul className={styles.alertMsg}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &#8251;현금은 사업단 지원금의 10% 이상을 의무 부담하여야 함</ul>
        <h2 className={styles.infoTitle}>지원분야</h2>
        <li>3개 클러스터 분야(반도체 &#8226; 디스플레이/ 자동차 &#8226; 부품/지능융합)</li>

        <h2 className={styles.infoTitle}>지원절차</h2>
        <div className={styles.process}>
          <div className={styles.bluePoint}>
            <p>
              01 <br />
              과제공고
            </p>
          </div>
          <img className={styles.arrow} src="Icon/arrow-right.png" alt="arrow right" />
          <div>
            <p>
              02 <br />
              사업계획서
              <br /> 접수
            </p>
          </div>
          <img className={styles.arrow} src="Icon/arrow-right.png" alt="arrow right" />
          <div>
            <p>
              03 <br />
              사업계획서
              <br /> 평가
            </p>
          </div>
          <img className={styles.arrow} src="Icon/arrow-right.png" alt="arrow right" />
          <div>
            <p>
              04 <br />
              과제선정
            </p>
          </div>
          <img className={styles.arrow} src="Icon/arrow-right.png" alt="arrow right" />
          <div>
            <p>
              05
              <br /> 수정사업계획서
              <br /> 제출
            </p>
          </div>
        </div>
        <div className={styles.downArrow}>
          <img className={styles.arrow} src="Icon/down-arrow.png" alt="down-arrow" />
        </div>
        <div className={styles.process}>
          <div className={styles.indigoPoint}>
            <p>
              10 <br />
              최종평가 <br /> 실시
            </p>
          </div>
          <img className={styles.arrow} src="Icon/left-arrow.png" alt="arrow right" />
          <div>
            <p>
              09 <br />
              최종 보고서
              <br /> 제출
            </p>
          </div>
          <img className={styles.arrow} src="Icon/left-arrow.png" alt="arrow right" />
          <div>
            <p>
              08 <br />
              진도보고 및
              <br /> 점검 실시
            </p>
          </div>
          <img className={styles.arrow} src="Icon/left-arrow.png" alt="arrow right" />
          <div>
            <p>
              07 <br />
              과제수행 및 <br /> 연구노트 작성
            </p>
          </div>
          <img className={styles.arrow} src="Icon/left-arrow.png" alt="arrow right" />
          <div>
            <p>
              06 <br />
              협약 및<br />
              기업부담금 납부
            </p>
          </div>
        </div>
      </div>

      <div className={styles.secondTitle}>산학공동연구 현황</div>
      <div className={`${styles.searchContainer} ${common.searchContainer}`}>
        <div className={common.searchWrap}>
          <select>
            <option value="default">내용</option>
          </select>
          <input placeholder="검색어를 입력하세요." />
          <img className={common.searchIcon} src="Icon/magnifyingGlass.png" alt="search" />
        </div>
        <select className={common.sort}>
          <option value="default">지원기관</option>
        </select>
      </div>
      <div className={styles.tableWrap}>
        <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
          <li>NO</li>
          <li>지원기관</li>
          <li>과제명</li>
          <li>과제책임자</li>
          <li>담당자</li>
          <li>계약일자</li>
          <li>연구비총액(원)</li>
        </ul>
        <Link to="/universityInfoDetailPage" className={styles.detailLink}>
          <ul className={`${common.graphList} ${styles.graphWidth}`}>
            <li>40</li>
            <li>(재)경남테크노파크</li>
            <li>지능형기계 제조엔지니어링 전문인력양성</li>
            <li>김병창</li>
            <li>3D융합기술센터</li>
            <li>2021-12-03</li>
            <li>62,500,000</li>
          </ul>
        </Link>
      </div>
      <Pagination />
    </div>
  );
};

export default UniversityInfo;
