import React from "react";
import styles from "./CorporateSupport.module.css";
import common from "../../../Style/CommonStyles.module.css";
import { divide } from "lodash";

const CorporateSupport = () => {
  return (
    <div className={styles.container}>
      <p className={styles.subTitle}>
        기업유형 및 성장단계에 맞게 기술개발부터 상용화까지 필요한 부분을 맞춤형으로 종합지원하는 프로그램입니다.
      </p>
      <div className={styles.containerWrap}>
        <div>
          <table className={styles.corporateTable}>
            <thead className={styles.corporateTh}>
              <tr>
                <th>프로그램명</th>
                <th>내용</th>
              </tr>
            </thead>
            <tbody className={styles.corporateTb}>
              <tr>
                <th>기업진단 AllSet 컨설팅</th>
                <td>
                  가족회사를 대상으로 현장방문 기업진단을 통하여 기업의 애로사항을 파악하고, 인적교류 및 산학관 연계프로그램을 전방위적으로
                  협력하기 위한 현장방문 기업 컨설팅
                </td>
              </tr>
              <tr>
                <th>단기기술지도</th>
                <td>
                  참여학부 교수가 보유하고 있는 기술을 이용하여 가족회사가 자체 연구인력으로서 해결하기 힘든 기술에 대한 애로기술 지도
                </td>
              </tr>
              <tr>
                <th>제안서 컨설팅</th>
                <td>
                  가족회사를 대상으로 중기청, 산통부, 미래부 또는 유관기관에서 수행하는 과제에 대하여 제안서작성 및 컨설팅을 통한 기업체의
                  기술개발 능력 향상 프로그램
                </td>
              </tr>
              <tr>
                <th>디자인 지원</th>
                <td>
                  디자인학부 교수가 보유하고있는 노하우를 바탕으로 가족회사의 디자인분야 애로사항을 기술지도를 통하여 지원하는 프로그램
                </td>
              </tr>
              <tr>
                <th>One-Stop 핵심기술 사업화 프로그램</th>
                <td>장비와 인력 인프라가 결합된 양방향 지원 시스템(장비지원 + 제작시스템 + 글로벌 마케팅)</td>
              </tr>
              <tr>
                <th>맞춤형 Start-Up 육성 프로그램</th>
                <td>창업한지 2년이내 업체를 대상으로 맞춤형 프로그램을 통한 창업기업 활성화 및 성장기반 구축</td>
              </tr>
              <tr>
                <th>클래스셀링 장기멘토링</th>
                <td>기업 실무자가 학교에서 상주하면서 특정기술에 대한 노하우를 직접 전수받는 장기기술지도 프로그램</td>
              </tr>
              <tr>
                <th>기술형 기업협력 프로그램</th>
                <td>
                  대학이 보유한 기술의 사업화를 위하여 유관기관(대학, 기술보증기금, 은행, 부산연합기술지주회사) 이 협력체계 구축한 프로그램
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <div className={styles.corporateSystem}>
          <div className={styles.step01}>
            <div>
              <p>기술형 기업으로 전환</p>
              <img src="Icon/upArrow.png" alt="아이콘" />
            </div>
            <div>
              <p>기술이전 기술개발인력</p>
              <img src="Icon/upArrow.png" alt="아이콘" />
            </div>
            <div>
              <p>기술사업화 아이템발굴</p>
              <img src="Icon/upArrow.png" alt="아이콘" />
            </div>
          </div>

          <h3>쌍방향 산학연계 시스템(LEGO 기술형 기업전환 지원)</h3>

          <div className={styles.step02}>
            <p>기업(가족회사)</p>
            <div>
              <span>상호협력</span>
              <img src="Icon/twoArrow.png" alt="아이콘" />
            </div>
            <p>대학</p>
            <div>
              <span>상호협력</span>
              <img src="Icon/twoArrow.png" alt="아이콘" />
            </div>
            <p>지역사회</p>
          </div>

          <div className={styles.step03}>
            <span>창업지원</span>
            <img src="Icon/longArrow.png" alt="아이콘" />
            <p>LEGO 산학협력 공동 인프라</p>
            <img src="Icon/longArrow.png" alt="아이콘" />
            <span>LEGO 지원</span>
          </div>

          <div className={styles.step04}>
            <p>창업지원단</p>
            <p>공용장비 지원센터</p>
            <p>기술경영센터</p>
            <p>산학 LEGO지원팀</p>
          </div>

          <div className={styles.step05}>
            <div>
              <em>공용장비 지원</em>
              <img src="Icon/upArrow.png" alt="아이콘" />
            </div>
            <div>
              <img src="Icon/upArrow.png" alt="아이콘" />
              <em>기술이전 지원</em>
            </div>
          </div>

          <img src="Icon/arrowBg.png" alt="배경" className={styles.arrowBg} />

          <div className={styles.step06}>
            <p>공용장비지원센터 연계 (동남권, 부산디자인센터)</p>
            <p>기술보증기금 (기술사업화 자원지원)</p>
            <p>부산지역 창업선도대학사업단</p>
            <p>유관기관 + 협회</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CorporateSupport;
