import React from "react";
import styles from "./FamilyCompany.module.css";
import common from "../../../Style/CommonStyles.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";
import DetailedSearch from "../../../Components/DetailComponents/DetailedSearch";
import Pagination from "../../../Components/DetailComponents/Pagination";

const FamilyCompany = () => {
  return (
    <div className={styles.container}>
      <p className={styles.subTitle}>
        가족회사란 대학과 기술지원, 공동연구, 현장실습, 취업, 교육 및 공용장비 활용, 지역협업 등의 유기적인 협력관계를 유지하기 위하여
        가족회사 가입 신청 및 승인을 통하여 사업단에 등록된 기업 및 기관, 유관 단체 등을 의미합니다.
      </p>
      <div className={common.containerWrap}>
        <div>
          <h2 className={styles.marginEdit}>등급별 혜택 및 참여 프로그램</h2>
          <div className={styles.membership}>
            <p>Silver</p>
            <p>Gold</p>
            <p>Platinum</p>
            <p>VIP</p>
          </div>
          <div className={styles.benefit}>
            <p>기술, 경영지도 최대 1회, 재직자교육(유료, 무료) 산학협력의회</p>
            <p>기술, 경영지도 최대 3회 지원, GNU DINO-LABs, 표준 현장실습학기제</p>
            <p>기술, 경영지도 최대 5회 지원, 산학공동기술개발과제, 국내외 전시회 참가 지원, 대학보유기술이전, 기념품 제공</p>
            <p>신규 산학협력 프로그램 개설시 우선 혜택 제공</p>
          </div>
        </div>

        <div>
          <h2>가입 절차</h2>
          <div className={styles.joinStep}>
            <div>
              <img src="Icon/family01.png" alt="아이콘" />
              <p>가입신청서 제출</p>
            </div>
            <FontAwesomeIcon icon={faArrowRight} className={styles.joinStepIcon} />
            <div>
              <img src="Icon/family02.png" alt="아이콘" />
              <p>검토 및 승인</p>
            </div>
            <FontAwesomeIcon icon={faArrowRight} className={styles.joinStepIcon} />
            <div>
              <img src="Icon/family03.png" alt="아이콘" />
              <p>협약 체결</p>
            </div>
            <FontAwesomeIcon icon={faArrowRight} className={styles.joinStepIcon} />
            <div>
              <img src="Icon/family04.png" alt="아이콘" />
              <p>프로그램 참여</p>
            </div>
          </div>
        </div>

        <div className={styles.situation}>
          <h2>가족회사 현황</h2>
          <DetailedSearch />
          <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
            <li>NO</li>
            <li>산업분류</li>
            <li>회사명</li>
            <li>대표자</li>
            <li>지역</li>
          </ul>
          <ul className={`${common.graphList} ${styles.graphWidth}`}>
            <li>07</li>
            <li>(F42122)보링, 그라우팅 및 관정 공사업</li>
            <li>서경건설(주)</li>
            <li>조상섭</li>
            <li>전남</li>
          </ul>
        </div>
        <Pagination />
      </div>
    </div>
  );
};

export default FamilyCompany;
