import React from "react";
import styles from "./Greeting.module.css";

const Greeting = () => {
  return (
    <div className={styles.container}>
      <div className={styles.greetingWrap}>
        <img className={styles.schoolImg02} src="Images/responsive/greeting01.jpg" alt="school" />
        <div className={styles.textWrap}>
          <p className={styles.title}>
            경남대학교 산학협력단 <br />
            웹사이트 방문을 환영합니다.
          </p>
          <br />
          <p className={styles.content}>
            경남대학교 산학협력단은 산학협력을 통한 지역산업발전과 고급인력 양성을 목적으로 2004년에 처음 문을 열었습니다. 또한 교수들의
            교내 연구 활동을 적극 지원하며 학문 발전과 더불어 연구역량강화를 통해 지역 발전에 실질적으로 기여할 수 있도록 2012년에 연구처를
            신설하고 산학협력단과 통합 운영하고 있습니다.
            <br />
            <br />
            그 동안 미흡했던 대학과 기업 간의 유기적인 협력이 산학협력단을 통해 가능해졌고, 그 속에서 기술개발의 효율성이 한층 높아졌으며,
            기업이 요구하는 기술 인력의 양성도 효과적으로 이뤄질수가 있었습니다.
            <br />
            <br />
            아울러 지역내 산업체 및 공공기관, 연구기관 등 다양한 교류협력 네트워크를 구축함으로써 실직적인 산학협력이 가능하게 되었습니다.
            <br />
            <br />
            앞으로 본교 산학협력단/연구처는 연구 및 산학협력의 성과가 국가산업의 경쟁 력 강화와 대학특성화의 핵심 관건임을 명심하고 그동안
            산업현장과 괴리 된 대학의 교육과 연구시스템을 재정비하여 신기술 창출과 상품화 개발이 효과적 으로 구현될 수 있도록 최선의 노력을
            다하고자 합니다.
            <br />
            <br />
            이를 위해 새롭게 단장하는 산학협력/연구처 홈페이지는 산학협력단에서 추진하 는 각종 사업뿐만 아니라 연구처에서 제공하는 교내의
            다양한 연구 활동 지원사 업과 정보를 신속히 전달하고 여러분의 소리를 귀담아 듣는 도우미로서 의 역할을 다할 것입니다.
            <br />
            <br />
            감사합니다.
            <br />
            <br />
            <br />
            <p className={styles.name}>산착부총장 겸 산학협력단장</p>
          </p>
        </div>
        <img className={styles.schoolImg} src="Images/GreetingPic.png" alt="school" />
      </div>
      <img className={styles.sign} src="Icon/sign.png" alt="signature" />
    </div>
  );
};

export default Greeting;
