import React from "react";
import styles from "./Industry.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass, faCaretDown } from "@fortawesome/free-solid-svg-icons";

const Industry = () => {
  return (
    <div className={styles.container}>
      <div className={styles.industryWrap}>
        <div className={styles.searchContainer}>
          <h3>이력정보 조회</h3>
          <select className={styles.sort}>
            <option value="기업명" className={styles.optionName}>
              <span>최신순</span>
            </option>
            <option value="최신순" className={styles.optionName}>
              조회순
            </option>
            <option value="업데이트순" className={styles.optionName}>
              업데이트순
            </option>
          </select>
          <FontAwesomeIcon icon={faCaretDown} className={styles.triangle} />
          <div className={styles.search}>
            <input type="text" placeholder="검색어를 입력해 주세요." className={styles.searchBtn} />
            <FontAwesomeIcon icon={faMagnifyingGlass} className={styles.searchIcon} />
          </div>
        </div>
        <div className={styles.productSearch}>
          <div>
            <img src="ICon/industryIcon01.png" alt="icon" />
            <h4>기업명 검색</h4>
            <p>기업명 키워드로 검색</p>
            <FontAwesomeIcon icon={faCaretDown} className={styles.arrowIcon} />
            <p>제품리스트에서 제품 선택</p>
            <FontAwesomeIcon icon={faCaretDown} className={styles.arrowIcon} />
            <p>제품상세정보 확인</p>
          </div>
          <div>
            <img src="ICon/industryIcon02.png" alt="icon" />
            <h4>제품명 검색</h4>
            <p>제품명 키워드로 검색</p>
            <FontAwesomeIcon icon={faCaretDown} className={styles.arrowIcon} />
            <p>제품리스트에서 제품 선택</p>
            <FontAwesomeIcon icon={faCaretDown} className={styles.arrowIcon} />
            <p>제품상세정보 확인</p>
          </div>
          <div>
            <img src="ICon/industryIcon03.png" alt="icon" />
            <h4>이력관리번호 검색</h4>
            <p>이력번호로 검색</p>
            <FontAwesomeIcon icon={faCaretDown} className={styles.arrowIcon} />
            <p>제품상세정보 확인</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Industry;
