import React from "react";
import styles from "./Rnd.module.css";
import common from "../../../Style/CommonStyles.module.css";
import DetailedSearch from "../../../Components/DetailComponents/DetailedSearch";
import Pagination from "../../../Components/DetailComponents/Pagination";

const Rnd = () => {
  return (
    <div className={styles.container}>
      <div className={common.containerWrap}>
        <div>
          <h2>연구과제 공고</h2>
          <table className={styles.rndTable}>
            <tbody className={styles.rndTb}>
              <tr>
                <td>전체</td>
                <td>개인정보보호위원회</td>
                <td>경찰청</td>
                <td>과학기술정보통신부</td>
                <td>교육부</td>
                <td>국무조정실</td>

                <td>국도교통부</td>
                <td>기상청</td>
                <td>기획재정부</td>
                <td>농림축산식품부</td>
                <td>농촌진흥청</td>
                <td>문화재청</td>

                <td>문화체육관광부</td>
                <td>보건복지부</td>
                <td>산림청</td>
                <td>산업통장자원부</td>
                <td>방위사업청</td>
                <td>식춤의약품안전처</td>
              </tr>
            </tbody>
          </table>
        </div>

        <div>
          <DetailedSearch />
          <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
            <li>NO</li>
            <li>공고명</li>
            <li>공고기관</li>
            <li>접수일</li>
            <li>마감일</li>
          </ul>
          <ul className={`${common.graphList} ${styles.graphWidth}`}>
            <li>07</li>
            <li>2023년 지역기업-청년 희망이음 지원사업 공고</li>
            <li>산업통산자원부</li>
            <li>2023-05-25</li>
            <li>2023-05-25</li>
          </ul>
        </div>
        <Pagination />
      </div>
    </div>
  );
};

export default Rnd;
